import React from 'react'
import ResourceService from '../Service/ResourceService'

class IotMenuResourceComponent extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            id:this.props.match.params.id,
            resources:[]
        }
    }
    
    componentDidMount(){
        ResourceService.IotMenuResource(this.state.id).then((response) => {
            this.setState({resources:response.data})
        });
    }

    render(){
       return(
        <div>
        <br></br>
        <h1 className="text-center font-weight-bold text-secondary">物管平台资源列表</h1>
        <table className="table table-striped table-boarder"> 
           <thead className="text-justify">
                <tr>
                 <th  className="text-secondary">id</th>
                  <th  className="text-secondary">资源名称</th>  
                  <th  className="text-secondary">描述信息</th>  
                  <th  className="text-secondary">创建时间</th>  
                  <th  className="text-secondary">最后一次更新时间</th> 
                  
                </tr>
                </thead>
             <tbody>
                 {
                     this.state.resources.map(
                         resource =>
                         <tr key= {resource.id}>         
                             <td>{resource.id}</td>
                             <td>{resource.resname}</td>
                             <td>{resource.description}</td>
                             <td>{resource.createtime}</td>
                             <td>{resource.updatetime}</td>
                             <td>
                             </td>
                         </tr>
                     )  
                 }
             </tbody>
        </table>
    </div>
       )
    }
}

export default new IotMenuResourceComponent 